import React from "react";

function MainLastSection() {
  return (
    <>
      <h1 className="main__lastSecTitle">advice for the public</h1>
      <h2 className="main__lastSecSub">
        Regularly and thoroughly clean your hands with an alcohol-based hand rub
        or wash them with soap and water. Why? Washing your hands with soap and
        water or using alcohol-based hand rub kills viruses that may be on your
        hands. Maintain at least 1 metre (3 feet) distance between yourself and
        others. Why? When someone coughs, sneezes, or speaks they spray small
        liquid droplets from their nose or mouth which may contain virus. If you
        are too close, you can breathe in the droplets, including the COVID-19
        virus if the person has the disease.
      </h2>
    </>
  );
}

export default MainLastSection;
